@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@100;400;700&display=swap);
body{font-family:'Roboto', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;background-image:linear-gradient(to right top, #051937, #002265, #002993, #0029c1, #121beb);color:white}*{margin:0;padding:0}#root{max-width:1200px;margin:0 auto}

.App{display:flex;justify-content:center;flex-direction:column;align-items:center}

.mainForm{max-width:500px;border:1px solid blue;padding:20px;border-radius:20px;display:flex;flex-wrap:wrap;justify-content:space-between;margin-top:10%}.mainForm input,.mainForm label{flex-basis:100%;padding:10px}.mainForm input[type='checkbox']{margin-right:10px;margin-top:15px}.mainForm button{flex-basis:45%;margin-top:15px;padding:10px;font-family:Roboto, sans-serif;font-size:15px}

.container{max-width:100%;padding:20px;border:1px solid green;border-radius:15px;margin-top:10px;display:flex;flex-wrap:wrap;justify-content:flex-start}.container div{margin:0 10px 10px 0;padding:5px;border:1px solid red;border-radius:5px;font-weight:bold;flex-grow:1;text-align:center}

